<template>
	<div>
		<h3>批量应用模板</h3> 
    <p>请点击左侧菜单，创建模板</p>

    
	</div>
</template>

<script>
  
  import {request} from "@/network/request.js";
  import Qs from 'qs';
    
    
  export default {
    data() {
      return {
        
        tplList:[],
        CampaignSchedule:[],
        drawer: false,
        direction: 'rtl',
        checkedList:[],
        schedule:'',
      }
    },

    methods:{
        getAreaTpl(){
          
          request({
            url:"/subway/getareaTpl",
            method: 'get',
          }).then( res => {            
            console.log(res)            
            this.tplList=res.data;
          })
        },
        getCampaignSchedule() {
          
            request({
              url:"/subway/getCampaignSchedule",
              method: 'get',            
            }).then( res => {            
              console.log(res)            

              console.log(typeof res.data )
              if(typeof res.data == "object")
              {
                this.CampaignSchedule=res.data;
              }

            })
          
          
        },
        deleteTpl(item) {
          
          this.$confirm('是否继续删除?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消'
                  }).then(() => {
                    
                    request({
                      url:"/subway/getAreaTpl",
                      method: 'POST',
                      data:Qs.stringify({
                        id:item.id,
                        job:"delete"
                      }),
                    }).then( res => {            
                      console.log(res)
                      
                      //去掉此条记录
                      this.getAreaTpl();
                    
                    })
                    this.$message({
                      type: 'success',
                      message: '删除成功!'
                    });
                  })

        },
        
        //打开计划选择框
        clickApply(item){
          this.schedule = item.tpl_content;
          this.drawer = true;
        },
        //应用到计划
        Apply()
        {
          
           console.log(this.CampaignSchedule);
          
          for(var i=0;i<this.checkedList.length;i++)
          {
        
            request({
              url:"/subway/updateCampaignArea",
              method: 'POST',
              data:Qs.stringify({
                campaign_id: this.checkedList[i],
                schedule:this.schedule,
              }),
            }).then( res => {
              console.log(res)
              if(typeof  res.data.data.campaign_area.campaign_id != 'undefined')
              {
                let cid = res.data.data.campaign_area.campaign_id;
                this.$message({
                  type: 'success',
                  message: cid+'设置成功!'
                });
              } else {
                
                this.$message({
                  type: 'fail',
                  message: '设置失败!'
                });
              }
              
              
            })
          }
        }
        
    },
    mounted() {
      this.getAreaTpl();
      
      this.getCampaignSchedule();
      
    },
    watch: {
        // 如果路由有变化，会再次执行该方法
        '$route': 'getAreaTpl'
      },
  }

</script>

<style>
  .el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
</style>
